import React, { useState, useEffect } from 'react';
import BpmControl from './BpmControl';
import audioEngine from './AudioEngine';
import './PlayerMenu.css';

const PlayerMenu = ({ bpm, onBpmChange }) => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isPlaying, setIsPlaying] = useState(false);

  //      
  useEffect(() => {
    //   
    setIsPlaying(audioEngine.isPlaying);

    //         
    const updatePlayingState = () => {
      setIsPlaying(audioEngine.isPlaying);
    };

    //     ,  AudioEngine   
    // audioEngine.addStateChangeListener(updatePlayingState);

    return () => {
      //   ,   
      // audioEngine.removeStateChangeListener(updatePlayingState);
    };
  }, []);

  //   /  BPM
  const toggleBpmMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  //   BPM
  const handleCloseMenu = () => {
    setIsMenuOpen(false);
  };

  //   BPM
  const handleBpmChange = (newBpm) => {
    onBpmChange(newBpm);
    //  BPM   
    audioEngine.setBPM(newBpm);
  };

  //    
  const handlePlayToggle = async () => {
    try {
      const playing = await audioEngine.togglePlay();
      setIsPlaying(playing);
    } catch (error) {
      console.error("   :", error);
    }
  };

  return (
    <div className="player-menu">
      <div className="menu-left">
        {/*  Play/Pause */}
        <button 
          className={`play-button ${isPlaying ? 'playing' : ''}`}
          onClick={handlePlayToggle}
        >
          {isPlaying ? '-' : '+'}
        </button>
      </div>

      <div className="menu-right">
        <button 
          className="bpm-toggle-button" 
          onClick={toggleBpmMenu}
        >
          {bpm} BPM
        </button>
      </div>

      {isMenuOpen && (
        <BpmControl 
          bpm={bpm} 
          onBpmChange={handleBpmChange} 
          onClose={handleCloseMenu}
        />
      )}
    </div>
  );
};

export default PlayerMenu;