/**
 * VisualScheduler -     ,
 *   
 */
class VisualScheduler {
  constructor() {
    //  
    this.isRunning = false;
    this.visualEvents = []; //   
    this.blockDuration = 0; //    
    this.totalBlocks = 0; //   
    this.totalDuration = 0; //   
    this.startTime = 0; //   
    this.pauseTime = 0; //  
    this.pausePosition = 0; //    

    // Callback   UI
    this.onVisualizationUpdate = null;

    //     
    this.activeNotes = new Set();

    //  
    this.currentBlockIndex = 0;

    //   requestAnimationFrame
    this.animationFrameId = null;

    //   
    this.audioEngine = null;
  }

  /**
   *      
   */
  setAudioEngine(audioEngine) {
    this.audioEngine = audioEngine;
  }

  /**
   *       
   */
  prepareVisualizationData(sequencerStructure, cellsState, bpm = 120) {
    //   
    this.visualEvents = [];
    this.totalBlocks = sequencerStructure.length;

    //      (4n =  )
    // 60 / bpm =     
    this.blockDuration = 60 / bpm;
    this.totalDuration = this.blockDuration * this.totalBlocks;

    let globalStartTime = 0;

    //      
    sequencerStructure.forEach((block, blockIndex) => {
      if (block && block.trackSteps) {
        Object.entries(block.trackSteps).forEach(([trackName, stepCount]) => {
          //     
          for (let step = 0; step < stepCount; step++) {
            const cellId = `${blockIndex}-${trackName}-${step}`;
            const cell = cellsState[cellId];

            if (cell && cell.instrumentId) {
              //   
              const stepDuration = this.blockDuration / stepCount;
              const startTime = globalStartTime + (step * stepDuration);
              const endTime = startTime + stepDuration;

              //   
              this.visualEvents.push({
                type: 'note',
                cellId,
                blockIndex,
                trackName,
                stepIndex: step,
                startTime,
                endTime,
                duration: stepDuration
              });
            }
          }
        });
      }

      //    
      this.visualEvents.push({
        type: 'blockChange',
        blockIndex,
        startTime: globalStartTime,
        endTime: globalStartTime + this.blockDuration
      });

      //   
      globalStartTime += this.blockDuration;
    });

    //     
    this.visualEvents.sort((a, b) => a.startTime - b.startTime);

    console.log(`Prepared ${this.visualEvents.length} visual events, total duration: ${this.totalDuration}s`);
    return true;
  }

  /**
   *  
   */
  start() {
    if (this.isRunning) return;

    this.isRunning = true;

    //     ,   
    if (this.audioEngine && typeof this.audioEngine.getPosition === 'function') {
      this.startTime = performance.now() / 1000 - this.audioEngine.getPosition();
    } else {
      //   ,       
      if (this.pausePosition > 0) {
        this.startTime = performance.now() / 1000 - this.pausePosition;
      } else {
        this.startTime = performance.now() / 1000;
      }
    }

    this.activeNotes.clear();

    //   
    this.animationLoop();
  }

  /**
   *   ()
   */
  pause() {
    if (!this.isRunning) return;

    this.isRunning = false;
    this.pauseTime = performance.now() / 1000;
    this.pausePosition = this.pauseTime - this.startTime;

    if (this.animationFrameId) {
      cancelAnimationFrame(this.animationFrameId);
      this.animationFrameId = null;
    }
  }

  /**
   *      
   */
  stop() {
    this.pause();
    this.pausePosition = 0;
    this.activeNotes.clear();

    //     
    if (this.onVisualizationUpdate) {
      this.onVisualizationUpdate({
        type: 'clear',
        activeNotes: [],
        currentBlockIndex: 0
      });
    }
  }

  /**
   *     
   */
  animationLoop() {
    if (!this.isRunning) return;

    let currentTime;

    //   ,     
    if (this.audioEngine && typeof this.audioEngine.getPosition === 'function') {
      currentTime = this.audioEngine.getPosition();
    } else {
      //    
      const now = performance.now() / 1000;
      let elapsedTime = now - this.startTime;

      //   (looping)
      currentTime = elapsedTime % this.totalDuration;
    }

    //      
    this.updateVisualState(currentTime);

    //    (60fps)
    this.animationFrameId = requestAnimationFrame(() => this.animationLoop());
  }

  /**
   *       
   */
  updateVisualState(currentTime) {
    //    
    const newBlockIndex = Math.floor(currentTime / this.blockDuration) % this.totalBlocks;
    if (newBlockIndex !== this.currentBlockIndex) {
      this.currentBlockIndex = newBlockIndex;

      //    
      if (this.onVisualizationUpdate) {
        this.onVisualizationUpdate({
          type: 'blockChange',
          currentBlockIndex: this.currentBlockIndex
        });
      }
    }

    //   
    const previousActiveNotes = new Set(this.activeNotes);
    this.activeNotes.clear();

    //   ,       
    for (const event of this.visualEvents) {
      if (event.type === 'note') {
        // ,        
        //       (50ms)
        const buffer = 0.05;
        if (currentTime >= event.startTime && 
            currentTime <= event.endTime + buffer) {
          this.activeNotes.add(event.cellId);
        }
      }
    }

    // ,     
    const notesChanged = 
      [...this.activeNotes].some(note => !previousActiveNotes.has(note)) || 
      [...previousActiveNotes].some(note => !this.activeNotes.has(note));

    //         
    if (notesChanged && this.onVisualizationUpdate) {
      this.onVisualizationUpdate({
        type: 'notesUpdate',
        activeNotes: [...this.activeNotes],
        currentBlockIndex: this.currentBlockIndex
      });
    }
  }

  /**
   *     
   * @param {Function} callback -      UI
   */
  setVisualizationUpdateCallback(callback) {
    if (typeof callback === 'function') {
      this.onVisualizationUpdate = callback;
    }
  }
}

//    
const visualScheduler = new VisualScheduler();
export default visualScheduler;